@import './constants.scss';

*::before,
*::after,
* {
    box-sizing: border-box;
}

html,
body {
    height: 100%;
    font-size: 62.5%; // 1rem = 10px, 10px/16px = 62.5%

    @media screen and (max-width: $xl) {
        font-size: 55%;
    }

    @media screen and (max-width: $lg) {
        font-size: 50%;
    }

    @media screen and (max-width: $md) {
        font-size: 40%;
    }
}

body {
    margin: 0;
    padding: 0;
    font-family: $mainFonts;
    line-height: 1.6;
    color: $grey;
}
